// Block button
// --------------------------------------------------

.xe-btn-block {
  display: block;
  width: 100%;
}

// Vertically space out multiple block buttons
.xe-btn-block + .xe-btn-block {
  margin-top: 5px;
}

// Specificity overrides
input[type="submit"],
input[type="reset"],
input[type="button"] {
  &.btn-block {
    width: 100%;
  }
}

.xe-btn-toggle {
  display: inline-block;

  input[type=checkbox] {
    opacity: 0;
    width: 0;
    height: 0;
    &:checked + .toggle {      
      background-color: #6f8dff;
      &:after {
        left: 41px;
        outline: none;
      }
      &:before{                
        left:11px;
        content:"ON";        
      }
    }
  }
  label {
    font-weight: 400;
    font-size: 13px;
    cursor: pointer;
    line-height: 30px;
  }
  .toggle {
    position: relative;
    display: inline-block;
    width: 68px;
    height: 28px;
    background-color: #dadbde;
    border-radius: 15px;
    margin-right: 10px;
    transition: background .3s ease;
    vertical-align: middle;
    content: "";
    &:after {
      display: inline-block;
      position: relative;
      left: 0;
      top: 1px;
      width: 26px;
      height: 26px;
      background-color: #fff;
      border-radius: 20px;
      border: 1px solid #e4e4e4;
      transition: left .3s ease,background .3s ease,box-shadow .1s ease;
      content: "";
    }
    &:before{
      position: absolute;
      right:10px;           
      content:"OFF";
      color:#fff;
    }
  }
}

@media \0screen\,screen\9 {
  .xe-toggle-button {
    input[type=checkbox] {
      width: auto;
      height: auto;
    }
    .toggle {
      display: none;
    }
  }
}
